import React from "react"
import { navigate } from "gatsby"
import { Link } from "gatsby"

const Card = ({  title,urlImg,id }) => (
  <Link to={`products/product${id}`} style={{ color:`black`,textDecoration:`none`}}class="card">
  <div class="cardContainer" >
  <img src={urlImg} class="cardImg" alt="Avatar"></img>
  <div class="container">
    <h4 class="cardTitle">{title}</h4> 
  </div>
  </div>
  </Link>
)


export default Card 
