import React from "react"
import Layout from "../components/layout"
import Card from "../components/Card"
import Posts from "../components/Posts"
import PostBlog from "../components/postsBlog"
import SEO from "../components/seo"
import HeadLine from "../components/HeadLine"
import { useStaticQuery, graphql } from "gatsby"



const IndexPage = () => {

   const data = useStaticQuery(graphql`
    query {
        productsJson{
            items{
              title
              description
              urlImg
              {
                img
              }
              id
            }
          }
        postsJson{
          items{
            title
            url
            description
            metaDescription
          }
        }
    }
  `);


  return(
    <>
  <Layout indexHeader={true} sideNavigation={false}>
    <SEO title="Home" />
    <div class="headLinesDistance">
    <HeadLine text="Ideas de cumpleaÃ±os para una amiga" />
    </div>
    <div class="posts">
    {data.postsJson.items.slice(0,3).map((item, key) => {
      return(<PostBlog url={item.url} description={item.description} title={item.title} metaDescription={item.metaDescription}/>)
    })}
    </div>
    <div class="headLinesDistance">
    <HeadLine text="Regalos graciosos para tu amiga" />
    </div>
    <div class="cards">
    {data.productsJson.items.map((item, key) => {
      return(<Card title={item.title} description={item.description} urlImg={item.urlImg[0].img} id={item.id} />)
    })}
    </div>
    <div style={{ height:`300px`}}>
    </div>
  </Layout>
  </>
  )
  }

export default IndexPage
